* {
  margin: 0;
}

body {
  background-color: #f5f6fa;
}

.app {
display: flex;
justify-content: space-evenly;
}

.app_left{
  flex: 0.9;
}
.app_header {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
  justify-content: space-between;
}

.app_stats {
  display: flex;
  justify-content: space-between;
}

@media (max-width: 998px) {
  .app {
      flex-direction: column;
  }
}
